import React, { useEffect, useState } from 'react';
import './App.css';
import Scrollbar from 'smooth-scrollbar';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

function App() {
	const [scroll, setScroll] = useState({ y: 0 });

	useEffect(() => {
		window.addEventListener('wheel', (e) => {
			setScroll({ y: scroll.y + e.deltaY });
		});
		window.scrollTo({ top: scroll.y });
		return () => {
			window.removeEventListener('wheel', () => {});
		};
	}, [scroll]);

	useEffect(() => {
		Scrollbar.init(document.body, {
			damping: 0.05,
			renderByPixels: true,
			continuousScrolling: true,
		});
	}, []);

	return (
		<motion.div
			id="scrl_cont"
			initial={{ opacity: 0, transition: { duration: 0.5 } }}
			animate={{ opacity: 1, transition: { duration: 0.5 } }}
			exit={{ opacity: 0, transition: { duration: 0.5 } }}
			key="home_screen"
		>
			{/* <motion.div
				initial={{ height: '100vh', transition: { duration: 0.5 } }}
				animate={{ height: 0, transition: { duration: 0.5 } }}
				exit={{ height: '100vh', transition: { duration: 0.5 } }}
				transition={{ duration: 0.5 }}
				className="trans"
			></motion.div> */}
			<div id="main_body">
				<div className="header">
					<h1 className="title">Meghan Gomez</h1>
					<Link to="gallery" className="gal_link">
						Gallery
					</Link>
				</div>
			</div>
		</motion.div>
	);
}

export default App;
