import React, { useEffect, useState } from 'react';
import Scrollbar from 'smooth-scrollbar';
import Image from './Image';
import { motion } from 'framer-motion';
const Gal = () => {
	const [scroll, setScroll] = useState({ y: 0 });
	const guidGenerator = () => {
		const S4 = function () {
			return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
		};
		return (
			S4() +
			S4() +
			'-' +
			S4() +
			'-' +
			S4() +
			'-' +
			S4() +
			'-' +
			S4() +
			S4() +
			S4()
		);
	};
	useEffect(() => {
		Scrollbar.init(document.body, {
			damping: 0.05,
			renderByPixels: true,
			continuousScrolling: true,
		});
	}, []);

	return (
		<motion.div
			id="holder"
			initial={{ opacity: 0, transition: { duration: 0.5 } }}
			animate={{ opacity: 1, transition: { duration: 0.5 } }}
			exit={{ opacity: 0, transition: { duration: 0.5 } }}
			key="gallery_screen"
		>
			{/* <motion.div
				initial={{ height: '100vh', transition: { duration: 0.5 } }}
				animate={{ height: 0, transition: { duration: 0.5 } }}
				exit={{ height: '100vh', transition: { duration: 0.5 } }}
				transition={{ duration: 0.5 }}
				className="trans"
			></motion.div> */}
			{new Array(43).fill(0).map(
				(_, i) =>
					i && (
						<div className="border_curve">
							<Image
								scroll={scroll}
								key={i}
								id={guidGenerator()}
								url={`/images/%20(${i}).jpg`}
							/>
						</div>
					)
			)}
		</motion.div>
	);
};

export default Gal;
