import React from 'react';
import { useEffect } from 'react';

const Image = ({ scroll, url, id, margin, onload }) => {
	useEffect(() => {
		const img = document.getElementById(id);
		// setMrgin(img.getBoundingClientRect().y / window.innerHeight);
	}, [window.pageYOffset, window.innerHeight, document.body.clientHeight]);
	return (
		<div
			className="imgDiv"
			id={id}
			style={{
				color: 'white',
				// backgroundImage: `url("${url}")`,
			}}
		>
			<img
				id={id}
				className={'imger'}
				src={url}
				onLoad={onload}
				style={{
					// transform: `scale(${Math.abs(mrgin * 0.1 + 0.9)})`,
					marginTop: margin,
				}}
				alt="A Sexy Bitch"
			/>
		</div>
	);
};

export default Image;
